import {
	passTime
} from '@/utils/index'
import {
	api
} from '@/const'

export default {
	props: {
		item: Object,
		hidden: {
			// 个人中心展示帖子隐藏一部分东西的复用
			default: false,
			type: Boolean
		},
		new_notice: {
			default: 1,
			type: Number
		}
	},
	onload() {},
	methods: {
		onShareAppMessage: function() {
			return {
				title: this.item.title, //分享内容
				path: '/pages/detail/main?postId=' + this.id + "&share=1" //分享地址
				// imageUrl: '/images/img_share.png',//分享图片
			}

		},
		previewImg(index) {
			const urlList = []
			for (var i = 0; i < this.item.imgList.length; i++) {
				urlList.push(this.item.imgList[i].url);
			}
			uni.previewImage({
				current: this.item.imgList[index].url, // 当前显示图片的http链接
				urls: urlList // 需要预览的图片http链接列表
			})
		},
		goAuthorPage(e) {
			const cachedUserId = uni.getStorageSync('userId')
			const cardUserId = e.currentTarget.dataset.userid
			if (cachedUserId == cardUserId) {
				uni.switchTab({
					url: `/pages/me/main`
				})
			} else {
				uni.navigateTo({
					url: `../user/main?otherUserId=${cardUserId}`
				})
			}
		},
		goDetail() {
			uni.navigateTo({
				url: `../detail/main?postId=${this.item.postId}`
			})
		},
		loadActions(e) {
			var that = this
			var t = uni.getStorageSync('t')
			var currentUserId = uni.getStorageSync('userId')
			var itemList = []
			if (currentUserId == this.item.userId) {
				itemList = ['删除', '收藏']
			} else {
				itemList = ['收藏']
			}

			uni.showActionSheet({
				itemList: itemList,
				success: function(res) {

					if (itemList.length == 2 && res.tapIndex == 0) {
						uni.showModal({
							title: '删除',
							content: '请确认此贴是否删除',
							success(res) {
								if (res.confirm) {
									uni.request({
										url: api,
										data: {
											act: 'post.delete',
											t: t,
											postId: that.item.postId
										},
										success: function(res) {
											if (res.data.code != null) {
												uni.showToast({
													title: '成功',
													icon: 'success',
													duration: 2000,
													success: function() {
														that.$emit('reloadCardList')
													}
												})
											}

										},
										fail: function(res) {
											uni.showToast({
												title: tips_msg.server_error,
												icon: 'none',
												duration: 2000
											})
										}
									})

								} else if (res.cancel) {}
							}
						})
					} else {
						// 收藏
					}
				},
				fail: function(res) {
					// console.log(res.errMsg)
				}
			})
		}
	}
}
